import React, { Component } from 'react';
import posed, { PoseGroup } from 'react-pose';
import TimelyBookButton from './TimelyBookButton';

const TAG_DELAY = 5000;
const IMG_DELAY = 7000;

const EaseIn = posed.div({
    enter: {
        opacity: 1,
        delay: 200
    },
    exit: {
        opacity: 0
    }
});

class Hero extends Component {
    constructor(props) {
        super(props);

        this.state = { currentTag: 0, bgImg: 0 };

        setTimeout(() => {
            this.updateTag();
        }, TAG_DELAY);

        setTimeout(() => {
            this.updateImg();
        }, IMG_DELAY);
    }

    componentDidMount() {
        this._ismounted = true;
    }

    componentWillUnmount() {
        this._ismounted = false;
    }

    updateImg = () => {
        if (!this._ismounted) return;

        if (this.state.bgImg + 1 >= this.props.imgs.length) {
            return this.setState({ bgImg: 0 }, () => {
                setTimeout(() => {
                    this.updateImg();
                }, IMG_DELAY);
            });
        }

        return this.setState({ bgImg: this.state.bgImg + 1 }, () => {
            setTimeout(() => {
                this.updateImg();
            }, IMG_DELAY);
        });
    }

    updateTag = () => {
        if (!this._ismounted) return;

        try {
            if (this.state.currentTag + 1 >= this.props.tags.length) {
                return this.setState({ currentTag: 0 }, () => {
                    setTimeout(() => {
                        this.updateTag();
                    }, TAG_DELAY);
                });
            }

            return this.setState({ currentTag: this.state.currentTag + 1 }, () => {
                setTimeout(() => {
                    this.updateTag();
                }, TAG_DELAY);
            });
        } catch (err) {
            setTimeout(() => {
                this.updateTag();
            }, TAG_DELAY);
        }
    }

    event = (type, path = null) => {
        // fire the event then do the navigation.
        window.dataLayer.push({ 'event': type });
        if (path) window.location = path;
    }

    render() {
        return (
            <section id="hero" className={`hero is-medium`}>
                {(this.props.imgs.map((img, i) => {
                    return (
                        <PoseGroup key={i}>
                            {(this.state.bgImg === i) &&
                                <EaseIn id="hero" key={`${i} hero`} className={`has-bg-img img-background-${i}`}>
                                    <div className="hero-body has-dot-overlay">
                                        <div className="container has-text-centered" id="hero-text">
                                            <h1 className="subtitle is-6 has-text-white has-text-weight-semibold">Undo, Skincare Center</h1>
                                            <h1 className="title has-text-white has-text-weight-bold hero-title">{this.props.tags[this.state.currentTag]}</h1>
                                            <h2 className="subtitle is-5 has-text-white has-text-weight-semibold">KANSAS CITY'S CHOICE IN SKINCARE!</h2>

                                            <div className="columns is-centered">
                                                {(this.props.isSelector === true) &&
                                                    <div className="column is-4">
                                                        <div href="/med_spa" onClick={() => { this.event('med_spa_nav', '/med_spa') }} className="button is-link is-medium is-fullwidth has-text-weight-semibold">MEDICAL SPA</div>
                                                    </div>
                                                }
                                                {(this.props.isSelector === true) &&
                                                    <div className="column is-4">
                                                        <div href="/tattoo" onClick={() => { this.event('tattoo_nav', '/tattoo') }} className="button is-link is-medium is-fullwidth has-text-weight-semibold">TATTOO REMOVAL</div>
                                                    </div>
                                                }

                                                {(this.props.isSelector !== true) &&
                                                    <div className="column is-3">
                                                        <TimelyBookButton className="button is-link is-medium is-fullwidth has-text-weight-semibold" />
                                                    </div>
                                                }
                                                {(!this.props.hidePricing) &&
                                                    <div className="column is-3">
                                                        <a className="button is-link is-medium is-fullwidth has-text-weight-semibold" href="/tattoo#pricing">Pricing</a>
                                                    </div>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </EaseIn>
                            }
                        </PoseGroup>
                    );
                }))}

                <style>{`
                    ${this.props.imgs.map((img, i) => {
                    return `
                        .img-background-${i} {
                            background: url('${img}') center 25%;
                            background-size: cover;
                        }`
                }).join('')}
                `}</style>
            </section>
        );
    }
}

Hero.defaultProps = {
    hidePricing: false,
    isSelector: false
}

export default Hero;