import React, { Component } from 'react';

class TimelyBookButton extends Component {
    constructor(props) {
        super(props);

        this._timely = new window.timelyButton("undolaserskincareclinic", { 'dontCreateButton': true });
    }

    event = () => {
        // fire the event then do the navigation.
        window.dataLayer.push({ 'event': 'book_now' });
        this._timely.start();
    }

    render() {
        return (
            <div className={this.props.container}>
                <a className="button is-link timely" {...this.props} onClick={this.event}>BOOK NOW</a>
            </div>
        );
    }
};

TimelyBookButton.defaultProps = {
    container: 'has-text-centered'
};

export default TimelyBookButton;